.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.block{
  width: 100vw;
  height:100vh;
  
}
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.Home-center{
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 1rem;
  margin-right: 1rem;
  /* */
  height: 100vh;
}
.Home-center .title{
  text-align: center;
  color: white;
  font-size: 3rem;
  font-family: 'Montserrat', sans-serif; 
  margin-bottom: 1rem;
}
.Home-center .links{
  width: auto;
  justify-content: space-evenly;
  display: flex;
  align-items: center;
}
.Home-center .links a{
  border-radius: 0.4rem;
  text-decoration: none;
  margin-right: 0.7rem;
  color: rgb(255, 255, 255);
  font-size: 1rem;
  font-family: 'Montserrat', sans-serif; 
  padding: 0.4rem;

}
.Home-center .links a:nth-child(1){
  background-color: black;
}
.Home-center .links a:nth-child(2){
  color: black;
  background-color: #ffae1a;

}
.Home-center .links a:nth-child(3){
  color: darkblue;
  background-color:white ;
}
.Home-center #resume-btn{
  text-align: center;
  text-decoration: underline;

  font-size: 1.3rem;
  margin-top: 0.7rem;
  font-family: 'Montserrat', sans-serif; 
  color: #ffffff;
  font-weight: bold;
}